import React, {Component} from 'react';
import {
    Page,
    Layout,
    FormLayout,
    Card,
    TextField,
    Select,
    Button,
    Banner
} from '@shopify/polaris';


class StatusBanner extends Component {

    state = {
        isOpenBanner: false,
        status: '',
        title: ''
    }

    componentDidMount() {
        this.setState({
            isOpenBanner: this.props.isOpenBanner, 
            status: this.props.status, 
            title: this.props.title
        })
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            isOpenBanner: nextProps.isOpenBanner,
            status: nextProps.status, 
            title: nextProps.title
        })
    }

    componentDidUpdate() {
        if (this.state.isOpenBanner) {
            setTimeout( () => {
                this.setState({isOpenBanner: false});
            }, 7000);
        }
    }

    render() {

        return (
            this.state.isOpenBanner && 
            <div style={{"marginBottom": 20}}>
            <Banner
                title={this.state.title}
                status={this.state.status}
            />
            </div>
        )
    }
}


class App extends Component {

    state = {
        data: {
            password: '',
            merchantAccount: '',
            SecretKey: ''
        },
        isOpenBanner: false,
        title: '',
        status: ''
    };

    handleChangeField = (value, key) => {
        let data = this.state.data;
        data[key] = value;
        this.setState({data: data});
    }

    handleSubmit = (event) => {
        event.preventDefault();
        fetch("https://wayforpay.trademinister.net/merchant", {
            method: "POST",
            body: JSON.stringify(this.state.data),
            headers: {
                "Content-Type": "application/json"
            },
            credentials: 'include'
        }).then(res => res.json()).then(data => {
            if(data.status) {
                this.setState({isOpenBanner: true, status: 'success', title: 'Save successfully'});
            }
        }).catch(error => {
            console.log(error);
            this.setState({isOpenBanner: true, status: 'critical', title: 'Error'});
        });
    }

    componentDidMount() {
        const URL = "https://wayforpay.trademinister.net/merchant";
        fetch(URL, {credentials: 'include'}).then(res => res.json()).then(json => {
            if (Object.keys(json).length) {
                console.log(json);
                this.setState({data: json});
            } else {
                console.log('merchant empty');
            }
        });
    }

    render() {
        return (
            <Page>
                
                <StatusBanner 
                    isOpenBanner={this.state.isOpenBanner} 
                    status={this.state.status}
                    title={this.state.title}
                />
                
                <form onSubmit={this.handleSubmit}>
                
                    <Layout>

                        <Layout.AnnotatedSection
                            title="Shopify payment"
                            description="WayForPay payment gateway">
                            <Card sectioned>
                            <FormLayout>
                                <TextField
                                    label="Password"
                                    name="password"
                                    value={this.state.data.password}
                                    onChange={(value, id) => {
                                        this.handleChangeField(value, 'password');
                                    }}
                                />
                            </FormLayout>
                            </Card>
                        </Layout.AnnotatedSection>

                        <Layout.AnnotatedSection
                            title="WayForPay payment"
                            description="WayForPay API access">
                            <Card sectioned>
                            <FormLayout>
                                <TextField
                                    label="merchantAccount"
                                    name="merchantAccount"
                                    value={this.state.data.merchantAccount}
                                    onChange={(value, id) => {
                                        this.handleChangeField(value, 'merchantAccount');
                                    }}
                                />
                                <TextField
                                    label="SecretKey"
                                    name="SecretKey"
                                    value={this.state.data.SecretKey}
                                    onChange={(value, id) => {
                                        this.handleChangeField(value, 'SecretKey');
                                    }}
                                />
                            </FormLayout>
                            </Card>
                        </Layout.AnnotatedSection>

                        

                        <Layout.AnnotatedSection>
                            <Button primary submit>Save</Button>
                        </Layout.AnnotatedSection>

                    </Layout>
                
                </form>

            </Page>
        );
    }
}

export default App;





